import React, { Component } from 'react';
import { Link, graphql } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { kebabCase } from 'lodash';
import { DiscussionEmbed } from 'disqus-react';
import Layout from '@common/layout';
import Footer from '@common/footer';
import Menu from '@common/menu';
import Bio from '@utils/bio';
import SEO from '@utils/SEO';
import { Section, Container } from '@styles/global';
import styled from 'styled-components';
import BackgroundImage from 'gatsby-background-image';
import defaultImage from '@static/cover.jpg';

const PrevNext = styled.ul`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    list-style: none;
    margin-left: 0;
    padding: 0;
`;

const Prev = styled.span`
    display: block;
`;

const Next = styled.span`
    display: block;
    text-align: left;
    @media (min-width: ${props => props.theme.screen.sm}) {
        text-align: right;
    }
`;

const TagList = styled.ul`
    list-style-type: none;
    margin-left: 0;

    li {
        display: inline;

        &:not(:last-child):after {
            content: ' • ';
        }
    }
`;

export const query = graphql`
    query($slug: String!) {
        site {
            siteMetadata {
                title
                social {
                    disqus
                }
            }
        }
        mdx(fields: { slug: { eq: $slug } }) {
            frontmatter {
                title
                date(fromNow: true)
                tags
                description
                image {
                    childImageSharp {
                        fluid(maxWidth: 1920, quality: 100) {
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
            }
            body
            timeToRead
        }
    }
`;

class PostTemplate extends Component {
    render() {
        const post = this.props.data.mdx;
        /** */
        const { image } = post.frontmatter;
        const imagePath = image && image.childImageSharp.fluid.src;
        const siteTitle = this.props.data.site.siteMetadata.title;
        /** */
        const { previous, next } = this.props.pageContext;
        /**  */
        const disqusShortname = this.props.data.site.siteMetadata.social.disqus;
        const disqusConfig = {
            identifier: post.id,
            title: post.frontmatter.title,
        };
        return (
            <Layout location={this.props.location} title={siteTitle}>
                <Menu accent="inverse" />

                <SEO
                    title={post.frontmatter.title}
                    description={post.frontmatter.description}
                    image={imagePath || defaultImage}
                    pathname={this.props.location.pathname}
                />

                {image != null && (
                    <BackgroundImage
                        fluid={post.frontmatter.image.childImageSharp.fluid}
                        style={{
                            height: `60vh`,
                            width: `100vw`,
                            backgroundPosition: 'center center',
                        }}
                    />
                )}

                <Section>
                    <Container>
                        <h1>{post.frontmatter.title}</h1>
                        <TagList className="label">
                            <li>{post.frontmatter.date}</li>
                            <li>{post.timeToRead} Min Read</li>
                            {post.frontmatter.tags.map(tag => (
                                <li key={tag + `tag`}>
                                    <Link to={`/tag/${kebabCase(tag)}/`}>
                                        {tag}
                                    </Link>
                                </li>
                            ))}
                        </TagList>
                        <hr />
                        <article>
                            <MDXRenderer>{post.body}</MDXRenderer>
                        </article>
                        <hr />
                        <PrevNext>
                            <li>
                                {previous && (
                                    <Link to={previous.fields.slug} rel="prev">
                                        <Prev className="label">Previous</Prev>
                                        {previous.frontmatter.title}
                                    </Link>
                                )}
                            </li>
                            <li>
                                {next && (
                                    <Link to={next.fields.slug} rel="next">
                                        <Next className="label">Next</Next>
                                        {next.frontmatter.title}
                                    </Link>
                                )}
                            </li>
                        </PrevNext>
                        <hr />
                        <Bio />
                        <DiscussionEmbed
                            shortname={disqusShortname}
                            config={disqusConfig}
                        />
                    </Container>
                </Section>
                <Footer />
            </Layout>
        );
    }
}

export default PostTemplate;
