import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import Image from 'gatsby-image';
import ExternalLink from '@utils/externalLink';
import styled from 'styled-components';

const Bio = styled.div`
    display: flex;
    align-items: center;
    flex-flow: column;

    @media (min-width: ${props => props.theme.screen.sm}) {
        flex-flow: row;
    }

    img {
        min-width: 60px;
        border-radius: 50%;
    }
`;

const Text = styled.p`
    font-size: 95%;
    margin: 1.5rem 0;

    @media (min-width: ${props => props.theme.screen.sm}) {
        margin: 0 1rem;
    }
`;

export default () => {
    const data = useStaticQuery(graphql`
        query {
            avatar: file(absolutePath: { regex: "images/avatar.png/" }) {
                childImageSharp {
                    fixed(width: 60, height: 60) {
                        ...GatsbyImageSharpFixed
                    }
                }
            }
            site {
                siteMetadata {
                    author
                    social {
                        twitter
                    }
                }
            }
        }
    `);
    const { author, social } = data.site.siteMetadata;
    return (
        <Bio>
            <Image
                style={{ padding: `0 1.6rem` }}
                fixed={data.avatar.childImageSharp.fixed}
                alt={author}
            />
            <Text>
                Written by <strong>{author}</strong>, a talented young freelance
                photographer and aspiring scientist imbued with superhuman
                abilities.{' '}
                <ExternalLink href={`https://twitter.com/${social.twitter}`}>
                    Follow him on Twitter
                </ExternalLink>{' '}
                but don't stalk him.
            </Text>
        </Bio>
    );
};
